.customizer{
	overflow: hidden;
	padding: 0 0 30px 0;
	position: relative;
	text-align: center;
	width: 100%;
}

/* Guitar
**************************************************************************************/

.guitar-container{
	box-shadow: 0 5px 25px rgba(0,0,0,0.3);
	display: inline-block;
	height: 80vh;
	margin: 0 auto;
	position: relative;
	text-align: center;
}

.guitar{
	height: 100%;
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
}

.guitar-base{
	display: block;
	height: 100%;
}

.guitar-color{
	mix-blend-mode: overlay;
	position: absolute;
	top: 52.1%;
	left: 24.7%;
	width: 50.1%;
	z-index: 2;
}
.guitar-color svg{
	display: block;
	height: auto;
	width: 100%;
}

.guitar-part{
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
}

.strings{
	background: url('../images/strings.png') no-repeat 0 0;
	background-size: 100% 100%;
	right: 0;
	bottom: 0;
	z-index: 10;
}

.flame{
	mix-blend-mode: multiply;
	z-index: 3;
}

.hardware{
	opacity: 0;
	position: absolute;
	top: 0.1%;
	left: -0.1%;
	z-index: 4;
	/*transition: opacity 0.3s;*/
}
.hardware.black{
	opacity: 1;
}

.pickup{
	background: no-repeat 0 0;
	background-size: 100% 100%;
	right: 0;
	bottom: 0;
	z-index: 5;
}

.pickup.neck.stock{
	background-image: none;
}
.pickup.neck.black{
	background-image: url('../images/pickup-neck-hb.png');
}
.pickup.neck.rails{
	background-image: url('../images/pickup-neck-rails.png');
}
.pickup.neck.mean90{
	background-image: url('../images/pickup-neck-90.png');
}

.pickup.bridge.stock{
	background-image: none;
}
.pickup.bridge.black{
	background-image: url('../images/pickup-bridge-hb.png');
}
.pickup.bridge.rails{
	background-image: url('../images/pickup-bridge-rails.png');
}
.pickup.bridge.mean90{
	background-image: url('../images/pickup-bridge-90.png');
}

/* Controls 
**************************************************************************************/

.controls-container{
	position: absolute;
	top: 0;
	left: 107%;
	width: 180px;
}

.controls{
	text-align: left;
	white-space: nowrap;
	width: 100%;
}

.controls fieldset{
	display: block;
	border: 0;
	padding: 0;
	margin: 0 0 20px 0;
	width: 100%;
}

.controls h2{
	color: #fff;
	font-size: 24px;
	margin: 0;
}

.input-group{
	margin-top: 5px;
}
.input-group:after{
	clear: both;
	content: '';
	display: block;
	width: 100%;
}

.controls label{
	color: #ddd;
	cursor: pointer;
	display: block;
	font-size: 18px;
}

.controls label.radio{
	display: block;
	margin-bottom: 10px;
	float: left;
	width: 50%;
}

.controls input[type="text"],
.controls input[type="number"]{
	background: #ddd;
	border: 0;
	border-radius: 5px;
	box-shadow: inset 0 3px 5px rgba(0,0,0,0.3);
	color: #000;
	display: block;
	font-family: 'Gaegu', cursive;
	font-size: 18px;
	font-weight: 700;
	margin-bottom: 20px;
	padding: 10px;
	width: 100%;
}

.controls input[type="radio"]{
}

.radio-proxy{
	background: #bbb;
	border-radius: 50%;
	box-shadow: inset 0 3px 5px rgba(0,0,0,0.3);
	display: inline-block;
	margin-right: 5px;
	width: 20px;
	height: 20px;
	position: relative;
	vertical-align: middle;
	transition: background 0.3s;
}
.radio-proxy:before{
	background: #000;
	border-radius: 50%;
	content: '';
	margin: auto;
	opacity: 0;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	width: 60%;
	height: 60%;
	transform: scale(0);
	transition: all 0.3s;
}

input:checked + .radio-proxy{
	background-color: #fff;
}
input:checked + .radio-proxy:before{
	opacity: 1;
	transform: scale(1);
}

.controls label.radio input{
	display: none;
}

.u-slider{
	border-radius: 3px;
	height: 6px;
	position: relative;
	margin: 15px 0 7px 0;
	width: 150px;
}
.u-slider + .u-slider{
	margin-top: 25px;
}

.u-slider .value{
	display: none;
}

.u-slider .handle{
	/*background: radial-gradient(#eee,#aaa);*/
	/*background: none;*/
	border: 3px solid #ddd;
	border-radius: 50%;
	cursor: grab;
	width: 20px;
	height: 20px;
	position: absolute;
	top: -7px !important;
	/*left: 0;*/
	transform: translateX(-50%);
}

.slider-red{
	background: linear-gradient(to right, #000,#f00);
}
.slider-green{
	background: linear-gradient(to right, #000,#0f0);
}
.slider-blue{
	background: linear-gradient(to right, #000,#00f);
}
.slider-flame{
	background: linear-gradient(to right, rgba(0,0,0,0), rgba(255,255,255,1));
}